import { handleActions } from "redux-actions"
import { GeoTypes } from '../actions'


export default handleActions({
    [GeoTypes.GET_GEO_INFOALL](state, action) {
        return Object.assign({}, state, (action.payload || {}))
    },
    [GeoTypes.GET_GEO_INFO](state, action) {
        return Object.assign({}, state, (action.payload || {}))
    },
    [GeoTypes.SET_GEO_KEYVALUE](state,action){
        return Object.assign({}, state, (action.payload || {}))
    },
    [GeoTypes.SET_GEO_OBJECT](state, action) {
        return Object.assign({}, state, (action.payload || {}))
    },
    [GeoTypes.SET_CURRENT_CITY](state,action){
        return Object.assign({}, state, (action.payload || {}))
    },
    [GeoTypes.SET_SELECT_CITY](state,action){
        return Object.assign({}, state, (action.payload || {}))
    }
}, {})