//延迟加载
module.lazyload = function (time) {
    if (!time) time = 50;
    let timeObj = {};
    return function (cb, time2 = time) {
        if (typeof cb != "function")
            return;
        clearInterval(timeObj);
        timeObj = setTimeout(cb, time2);
    }
};

