window.addEventListener("mousewheel", (e) => {
    if (e.deltaY === 1) {
        e.preventDefault();
    }
});


// lat obj={a:{b:{c:{d:1}}}}  valueGet(obj,'a.b.c.d')==1
module.valueGet = (obj, express) => {
    let array = express.split('.') || []
    let result = null;
    if (!obj) {  //为null 或者 undefine 直接返回null
        return result
    }
    let preValue = obj
    for (let index = 0; index < array.length; index++) {
        const element = array[index];
        if (preValue && preValue.hasOwnProperty(element)) {
            preValue = preValue[element]
            result = preValue
        } else {
            result = null;
            break;
        }
    }
    return result
}

