import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import asyncComponent from '@/utils/asyncComponent';
import Navigation from "@/view/Navigation";
import home from "@/view/Home/Index";
const communityrecommend = asyncComponent(() => import("@/view/Community/IndexRecommend"));
const communityfortune = asyncComponent(() => import("@/view/Community/IndexFortune"));
const search = asyncComponent(() => import("@/view/Search/Index"));
const activity = asyncComponent(() => import("@/view/Activity/Index"));
const myactivity = asyncComponent(() => import("@/view/Activity/MyIndex"));
const selectcity = asyncComponent(() => import("@/view/SelectCity/Index"));
const comment = asyncComponent(() => import("@/view/Comment/Index"));
const commentlist = asyncComponent(() => import("@/view/Comment/CommentList"));
const businessdepart = asyncComponent(() => import("@/view/BusinessDepart/Index"));

export default class RouteConfig extends Component{
  render(){
    return(
      <Router>
        <Switch>
          <Route path="/" exact component={Navigation} />
          <Route path="/home" component={home} />
          <Route path="/communityrecommend" component={communityrecommend} />
          <Route path="/communityfortune" component={communityfortune} />
          <Route path="/search" component={search} />
          <Route path="/activity" component={activity} />
          <Route path="/myactivity" component={myactivity} />
          <Route path="/selectcity" component={selectcity} />
          <Route path="/comment" component={comment} />
          <Route path="/commentlist" component={commentlist} />
          <Route path="/businessdepart" component={businessdepart} />
          <Redirect to="/" />
        </Switch>
      </Router>
    )
  }
}
