import { fortuneInstance } from './axiosex'
export const GetCFHUserInfoByUid = (obj) =>{
    fortuneInstance.post(`SpecialTopic/api/PersonalPage/GetCFHUserInfoByUid`,obj)
        .then(function (json) {
            return json
        })
        .catch(function (error) {
            console.log(error)
            return null
        })
}