import React from 'react';
import ReactDOM from 'react-dom';


const styles = {
  mask: {
    position: 'fixed',
    top: 0,
    right: 0,
    left: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.85)',
    zIndex: 99
  },
  modalBox: {
    position: 'fixed',
    top: '50%',
    left: '50%',
    width: '82%',
    zIndex: 100,
    transform:'translate(-50%,-50%)',
    paddingTop:30,
    fontSize: 14,
    backgroundColor: '#fff',
    borderRadius: 4,
    overflow: 'hidden',
    textAlign: 'center'
  },
  modalTitle:{
    color:'#282828',
    fontSize:17,
    fontWeight:'bold'
  },
  modalContent:{
    padding:'10px 0 30px',
    color:'#444',
    fontSize:15
  },
  btnGroup: {
    position:'relative',
    display:'flex',
    height:50,
    alignItems:'center',
    justifyContent:'center'
  },
  btnline:{
    position:'absolute',
    left:'50%',
    height:100,
    width:1,
    bottom:0,
    background:'#e5e5e5',
    transform:'scale(0.5) translate(-50%,50%)'
  },
  btnList:{
    flex:1,
    color:'#444',
    fontSize:17,
    height:50,
    display:'flex',
    alignItems:'center',
    justifyContent:'center'
  },
  btnBlue:{
    color:'#3381e3'
  }
};

export default {
    dom: null,
    open ({title, content, onOk, onCancel}) {
        this.close();
        this.dom = document.createElement('div');
        const JSXdom = (
            <div>
                <div style={styles.mask} />
                <div style={styles.modalBox}>
                  <h2 style={styles.modalTitle} dangerouslySetInnerHTML={{ __html: title }}></h2>
                  <p style={styles.modalContent} dangerouslySetInnerHTML={{ __html: content }}></p>
                  <div style={styles.btnGroup}>
                    <i style={styles.btnline}></i>
                    <span style={styles.btnList} onClick={() => this.onCancel(onCancel)}>取消</span>
                    <span style={{...styles.btnList,...styles.btnBlue}} onClick={() => this.onOk(onOk)}>确定</span>
                  </div>
                </div>
            </div>
        );
        ReactDOM.render(JSXdom, this.dom);
        document.body.appendChild(this.dom);
    },
    onCancel (onCancel) {
        (onCancel instanceof Function) && onCancel();
        this.close();
    },
    onOk (onOk) {
        (onOk instanceof Function) && onOk();
        this.close();
    },
    close () {
        this.dom && this.dom.remove();
    }
}

