const localStorage = window.localStorage
const oneMinute = 60 * 1000
export default {
    // geo:{
    //     source:{
    //         geoChangeCityTime:'',     //切换城市弹框的过期时间
    //         historyCity:'',           //定位城市
    //         selectCity:'',            //手动选择的城市
    //         selectCityExpireTime:''   //选择的城市过期时间
    //         geoRejectTime:'',   //不允许访问位置权限弹框的过期时间
    //     },
    //     _expire:     过期时间
    // }

    // 原先的localStorage getItem 方法
    originGetItem: localStorage.getItem,
    // 原先的localStorage setItem 方法
    originSetItem: localStorage.setItem,
    // 判断是否支持localStorage
    checkLocalStorage (){
        if (!localStorage) {
            console.log('不支持localStorage')
            return false
        }
        return true
    },
    getOriginItemAsObj (table ){
        let tabStr = localStorage.getItem(table)
        let tabObj = null
        if (tabStr) {
            try {
                tabObj = JSON.parse(tabStr)
            } catch{
                tabObj = null
            }
        }
        return tabObj
    },
    // 从localStorage里面获取指定KEY的值，并转换为对象
    getItemAsObj (table) {
        let tabObj = this.getOriginItemAsObj(table)
        // 判断是否过期，如果过期返回null，并清除原先的值
        if(tabObj){
            let expire = tabObj['_expire']
            if(!expire){
                return tabObj.source || tabObj
            }
            let expireDate = new Date(expire)
            if(expireDate<new Date()){
                tabObj = null
                localStorage.removeItem(table)
            }
        }

        return !tabObj ? null : tabObj.source
    },
    /**
     * 设置指定值得过期时间
     * @param {String} table 外层table的名称
     * @param {Float} _expire 过期时间，默认为一天，单位为分钟
     */
    setExpire(table, _expire = 24 * 60){
        if (!this.checkLocalStorage()) {
            return false
        }
        let tabObj = this.getOriginItemAsObj(table)
        if(!tabObj){
            return false
        }
        var t = new Date()
        var t_s = t.getTime()

        tabObj['_expire'] = t.setTime(t_s + _expire * oneMinute)
        // this.setItem(table, tabObj)
        localStorage.setItem(table, JSON.stringify(tabObj))
    },
    /**
     * 设置指定table里的Key-Value
     * @param {String} table 最外层的table名称
     * @param {any} key 内层key,如果是object类型，就作为table的value存到localStorage里面
     * @param {any} value key 对应的value
     */
    setItem(table, key, value) {
        if (!this.checkLocalStorage()) {
            return
        }
        let tabObj = this.getOriginItemAsObj(table) || {}
        let source = tabObj.source || {}
        if(typeof key === 'object'){
            Object.assign(source,key)
        }else{
            source[key] = value
        }
        tabObj.source=source
        localStorage.setItem(table,JSON.stringify(tabObj))
        // let geoObj = {
        //     geoRejectTime: '',
        //     geoChangeCityTime: '',
        //     historyCity: ''
        // }
        // let geoString = JSON.stringify(geoObj)
        // window.localStorage.setItem('geoObj', geoString)
    },
    getItem (table, key) {
        if (!this.checkLocalStorage()) {
            return
        }
        let tabObj = this.getItemAsObj(table)
        if(!tabObj){
            return null
        }
        if(key){
            return tabObj[key]
        }
        return tabObj
    }
}