import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import PropTypes from 'prop-types'
export default class EmInput extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
    this.state = {
      value:''
    }
    this.handelSearch = this.handelSearch.bind(this)
    this.clearInput = this.clearInput.bind(this)
  }
  static propTypes = {
    onChange: PropTypes.func
  }
  handelSearch(e){
    let value = e.target.value
    this.setState({
      value: value
    })
    this.props.onChange && this.props.onChange(value)
  }
  clearInput(){
    this.setState({
      value: ''
    })
    this.props.onChange && this.props.onChange('')
  }
  render () {
    let { value } = this.state
    return (
      <div className="flex-box">
        <input {...this.props} onChange={this.handelSearch} className="input" placeholder="搜索你感兴趣的社区/活动/帖子" type="text" name="" value={value} />
        <i onClick={this.clearInput} className={value.length>0?"input-clear":'hide'}></i>
      </div>
    )
  }
}
