//异步 让dispatch可以接收一个promise对象，然后等待返回后再去执行dispatch
import promiseMiddleware from "redux-promise";
import {createStore, applyMiddleware} from 'redux'
//处理异步action
import thunk from "redux-thunk"
//日志记录
import { createLogger } from "redux-logger"
import reducers from './reducers'
//可视化调试工具
import { composeWithDevTools } from "redux-devtools-extension"
//用applyMiddleware方法来使用一个或多个中间件，将它作为createStore的第二个参数传入即可
//thunk中间件，可以实现异步action；中间件的作用就是通过改变dispatch方法来改变数据流
//同步的action返回的是一个对象，异步的action返回的是一个函数
//如果action的类型是object，说明是一个同步的，直接dispatch就好了；如果action的类型是function，当触发这个dispatch的时候，就触发action这个函数，同时将dispatch和getState方法传入到action函数中，这就是为什么我们能在异步action中拿到dispatch和getState方法的原因
export default createStore(reducers, composeWithDevTools(applyMiddleware(promiseMiddleware,thunk,createLogger())))