import { createAction } from 'redux-actions'
import { FortuneTypes } from "./types"
import {GetCFHUserInfoByUid} from '../requests/fortune'
import common from '../../utils/login'
export const getCFHUserInfoByUid = createAction(FortuneTypes.FORTUNE_USER_INFO, async (obj) => {
    let result = await GetCFHUserInfoByUid(obj)
    // alert(JSON.stringify(result))
    let item = common.valueGet(result,'data.Result.Item')
    return item
})