import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import { Link } from 'react-router-dom';
import asyncComponent from '@/utils/asyncComponent';
const home = asyncComponent(() => import("@/view/Home/Index"));
const communityrecommend = asyncComponent(() => import("@/view/Community/IndexRecommend"));
const communityfortune = asyncComponent(() => import("@/view/Community/IndexFortune"));
const search = asyncComponent(() => import("@/view/Search/Index"));
const activity = asyncComponent(() => import("@/view/Activity/Index"));
const myactivity = asyncComponent(() => import("@/view/Activity/MyIndex"));
const selectcity = asyncComponent(() => import("@/view/SelectCity/Index"));
const comment = asyncComponent(() => import("@/view/Comment/Index"));
const commentlist = asyncComponent(() => import("@/view/Comment/CommentList"));
const businessdepart = asyncComponent(() => import("@/view/BusinessDepart/Index"));

const routeArr= [
  { path:'home',name:'首页' },
  { path:'communityrecommend',name:'推荐社区' },
  { path:'communityfortune',name:'财富社区' },
  { path:'myactivity',name:'我的活动' },
  { path:'activity',name:'精彩活动' },
  { path:'search',name:'搜索' },
  { path:'selectcity',name:'选择城市' },
  { path:'comment',name:'活动评价' },
  { path:'commentlist',name:'精彩评价' },
  { path:'businessdepart',name:'营业部主页' }
]
export default class Navigation extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.toggleTheme = this.toggleTheme.bind(this);
  }
  componentDidMount (){
    [...document.querySelectorAll('.navigation-list a')].forEach(item => {
      item.style.background = `#${Math.random().toString(16).slice(-6)}`
    })
  }
  toggleTheme(){
    let themeClass = document.body.className
    document.body.className = themeClass ? '' : 'theme-w'
  }
  render() {
    return (
      <div className="navigation-list">
        {
          routeArr.map((item,index)=>{
            return (
              <Link key={index} to={item.path}>{item.name}</Link>
            )
          })
        }
        <a onClick={this.toggleTheme} className="change-theme">切换皮肤</a>
      </div>
    )
  }
}
