import axios from 'axios'
import config from '../../utils/config'
let transformRequest = [
    data => typeof data == 'object' ? JSON.stringify(data) : data
]

let transformResponse = [
    data=>data
]

let timeout = 3000

// 由于使用了redux-promise，因而将所有的返回都识别为正常返回，在做业务处理的时候判断数据的正确性
let validateStatus = () => true // (status>=200 && status<300)

axios.defaults.headers.post['Content-Type'] ='application/json'
axios.defaults.responseType = 'json'
axios.defaults.transformRequest = transformRequest
axios.defaults.transformResponse = transformResponse
axios.defaults.timeout = timeout
axios.defaults.validateStatus = validateStatus
// 在请求前拦截请求
axios.interceptors.request.use( config => {
    return config
}, error => {
    return Promise.resolve({data:{
        Status: -100,
        Message: '请求有误',
        OtherInfo: error
    }})
})

// 在请求之后处理请求之前拦截
axios.interceptors.response.use( response => {
    return response
}, error => {
    let r = {
        data:{
            Status: -101,
            Message: '返回有误',
            OtherInfo: error
        }
    }
    return Promise.resolve(r)
})

export const activityInstance = axios.create({
    //baseURL: 'http://61.129.248.162:7114/infoWealthCommunityService'
})

export const fortuneInstance = axios.create({
    baseURL : config.api_person
})

export default axios