import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import IndexHeader from './IndexHeader'
import NearCommunity from './NearCommunity'
import HotActivity from './HotActivity'
import HotCommunity from './HotCommunity'
import HotPosts from './HotPosts'

export default class App extends Component {
  constructor (props) {
    super(props)
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {
      timeArr:['发帖时间排序','评论时间排序'],
      timeIndex:0 //发帖时间 0 发帖时间排序  1 评论时间排序
    }
  }
  changeTimeTab = (index) => {
    index!==this.state.timeIndex && this.setState({
      timeIndex : index
    })
  }
  render() {
    let { timeIndex,timeArr} =this.state
    return (
      <div className="wrapper">
        <IndexHeader />
        <NearCommunity />
        <HotActivity />
        <HotCommunity />
        <div className="com_tit">热门帖子
          <ul className="chosen-list">
            {
              timeArr.length>0 && timeArr.map((item,index) =>{
                return (
                  <li key={index} onClick={ this.changeTimeTab.bind(this,index) } className={ index==timeIndex ? 'active':'' }>{item}</li>
                )
              })
            }
          </ul>
        </div>
        <HotPosts />
        {/* <div className={showLocation ? 'mask' : ''}></div>
        <div className={showLocation ? 'show-maploaction' : 'hide'}>
          <img className="maplocation-icon" src={require('../../assets/map-location.png')} alt="" />
          <p className="loaction-tit">开启地理位置权限</p>
          <p className="loaction-content">为你推荐身边的营业部<br />发现更多精彩线下活动</p>
          <ul className="mapbtn-list">
            <li>不允许</li>
            <li className="allow-list">允许</li>
          </ul>
          <a onClick={this.closeMap} className="close-location"></a>
        </div> */}
      </div>
    )
  }
}
