import { GeoTypes } from './types'
import { createAction } from "redux-actions"
import localStorage from "../../utils/extLocalStorage"

export const getGeoInfoAll = createAction(GeoTypes.GET_GEO_INFOALL,()=>{
    return localStorage.getItemAsObj('geo')
})
export const getGeoInfo = createAction(GeoTypes.GET_GEO_INFO, (key) => {
    var obj={}
    obj[key] = localStorage.getItem('geo', key)
    return obj
})
export const setGeoKeyValue = createAction(GeoTypes.SET_GEO_KEYVALUE, (key,value) => {
    localStorage.setItem('geo', key, value)
    //localStorage.setExpire('geo', 5)
    return localStorage.getItemAsObj('geo')
})

export const setGeoObject =  createAction(GeoTypes.SET_GEO_OBJECT, current => {
    localStorage.setItem('geo', current)
    //localStorage.setExpire('geo', 5)
    return localStorage.getItemAsObj('geo')
})
export const setCurrentCity = createAction(GeoTypes.SET_CURRENT_CITY, cityname =>{
    return {'currentCity':cityname}
})
export const setSelectCity = createAction(GeoTypes.SET_SELECT_CITY, selectcityname => {
    return { 'selectCity': selectcityname }
})