import { createAction } from 'redux-actions'
import { ActivityTypes } from "./types"
import * as request from '../requests/activities'


export const getAll = createAction(ActivityTypes.GET_ALL, async ()=>{
    let result = await request.api1()
    return result.data
})

export const setOne = createAction(ActivityTypes.SET_ALL, async one => {
    return await request.api2(one).data
})

