import React, { Component } from 'react'
import {connect} from 'react-redux'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import ReactSwipe from 'react-swipe'
import EmInput from '@/components/EmInput'
import { emH5GetLocation } from 'emf-native/lib/native/native.info'
import { setTitle, emH5ToPersonalPage } from 'emf-native/lib/native/native.page'
import {withRouter} from 'react-router-dom'
import Message from '@/components/Message'
import {geo} from '../../store/actions'
import common from '../../utils/login'
import { fortuneInstance } from '../../store/requests/axiosex'
//import {} from '../../resourceimport/normalReact'
//import PropTypes from 'prop-types';

class IndexHeader extends Component {
  constructor(props) {
    super(props)
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {
      index: 0,
      ImgList: [
        'http://10.10.82.235:9998/WAP/wap18cn/resource/img/advertising.png',
        'http://10.10.82.235:9998/WAP/wap18cn/resource/img/finance-bg.png'
      ],
      location: '全国',
      showLocationAlert:false,     //是否显示请求权限弹框
      showChangeAlert:false,       //是否显示切换城市弹框
      geoUseInterface:false,       //用接口请求到的地理位置
      geoUseLocation:false,        //用交互GPS得到的地理位置
      localStorageGeoInfo: this.props.getGeoInfoAll().payload   //从localStorage中取出所有的geo信息
    }
  
    this.closeMap = this.closeMap.bind(this)
    //this.getPermission = this.getPermission.bind(this)
    //this.toOtherPage = this.toOtherPage.bind(this)
    //this.toPersonalPage = this.toPersonalPage.bind(this)
    //this.toMyActivictyPage = this.toMyActivictyPage.bind(this)
  }
  //跳转到其他页面
  toOtherPage = (path) => {
    this.props.history.push(path)
  }
  //登录通行证判断,未登录则去登录
  checkLogin = (callback) => {
    common.checkLogin(function(json){
      console.log(json)
      if (json.isLogin == true){
        callback && callback(json)
      }
    },true)
  }
  //点击我的帖子 跳转财富号个人页
  toPersonalPage = () =>{
    this.checkLogin(function(json){
      if(json.passport){
        let uid = json.passport.uid 
        fortuneInstance.post(`SpecialTopic/api/PersonalPage/GetCFHUserInfoByUid`, {
          //'AuthorUid': '9501015422651982'
          'AuthorUid': uid
        })
          .then(function (json) {
            let item=common.valueGet(json,'data.Result.Item')
            if (item) {
              let authorId = item.AuthorId
              let userType = 2
              if (!authorId) {
                userType = 1
              }
              console.log(userType)
              emH5ToPersonalPage(uid, 1, userType)
            }
          })
          .catch(function (error) {
            console.log(error);
          })
      }
    })
  }
  // toPersonalPage = () => {
  //   this.checkLogin((json)=>{
  //     if (json.passport) {
  //       let uid = json.passport.uid
  //       let item = this.props.getCFHUserInfoByUid({'AuthorUid': uid}).payload
  //       if (item) {
  //         let authorId = item.AuthorId
  //         let userType = 2
  //         if (!authorId) {
  //           userType = 1
  //         }
  //         emH5ToPersonalPage(uid, 1, userType)
  //       }
  //     }
  //   })
  // }
  //点击我的活动 显示该通行证下报名过的活动
  toMyActivityPage = () =>{
    this.checkLogin((json)=>{
      if(json.passport){
        let uid = json.passport.uid
        this.props.history.push('/myactivity') 
      }
    })
  }
  //是否切换城市弹框
  showModal = (historyCity, currentCity)=> {
    Message.open({
      title: '预约确认',
      content: `系统定位您在${currentCity}，是否切换？`,
      onCancel: () => {
        this.setState({
          location:historyCity
        })
        if(this.state.localStorageGeoInfo.selectCity){
          let newExpireTime = this.state.localStorageGeoInfo.selectCityExpireTime + 24*3600*1000
          this.props.setGeoObject({'selectCityExpireTime': newExpireTime})
        }
      },
      onOk: () => {
        this.setState({
          location: currentCity
        })
        let changecitytime = new Date().getTime()
        let changecitytimeexpire = changecitytime + 24*3600*1000
        this.props.setGeoObject({ 'historyCity': currentCity, 'geoChangeCityTime': changecitytimeexpire })
      }
    })
  }
  //请求权限弹框 关闭或不允许
  closeMap = () => {
    let rejectTime = new Date().getTime()
    let rejectTimeExpire = rejectTime + 48*3600*1000
    this.setState({
      showLocationAlert: false,
      location: '全国'
    })
    this.props.setGeoObject({ 'geoRejectTime': rejectTimeExpire,'historyCity':'全国'})
  }
  //请求权限弹框 允许
  getPermission = () => {
    this.setState({
      showLocationAlert: false
    })
    emH5GetLocation((json) => {
      if (json.haspermission == true && json.longitude && json.latitude) {
        this.setState({
          showChangeAlert: false
        })
        this.getDetailLocation(json.longitude, json.latitude)
      } else if (json.haspermission == false && !json.longitude && !json.latitude) {
        this.setState({
          location: '全国'
        })
        this.props.setGeoObject({'historyCity': '全国' })
      }
    }, {
        "forceGeoPermission": 2
      })
  }
  //根据经纬度获取具体位置信息
  getDetailLocation = (longitude, latitude) =>{
    let point = new BMap.Point(longitude, latitude)
    // GPS坐标转换为百度API坐标
    let convertor = new BMap.Convertor()
    let pointArr = []
    pointArr.push(point)
    convertor.translate(pointArr, 1, 5, (datat)=>{
      //console.log(datat)
      if (datat.status === 0) {
        // 根据百度API坐标得到具体位置信息 
        let translatedPoint = datat.points[0]
        let geoc = new BMap.Geocoder()
        geoc.getLocation(translatedPoint,(rs)=>{
          //console.log(rs)
          let addComp = rs.addressComponents;
          let site = addComp.province + "," + addComp.city + "," + addComp.district + "," + addComp.street + ", " + addComp.streetNumber;
          console.log('您的详细地址是：' + site)
          //若没有历史城市记录，添加；若定位城市改变，弹切换城市框
          let currentCity = addComp.city.substr(-1) == '市' ? addComp.city.substr(0, addComp.city.length-1) : addComp.city
          let historyCity = this.state.localStorageGeoInfo.historyCity
          let selectCity = this.state.localStorageGeoInfo.selectCity
          if (this.state.showChangeAlert && selectCity && selectCity !== currentCity){
            this.showModal(selectCity, currentCity)
            return
          }
          if (this.state.showChangeAlert && historyCity && historyCity !== currentCity){
            this.showModal(historyCity, currentCity)
          }else{
              this.setState({
                location: currentCity
              })
            this.props.setGeoObject({ 'historyCity':currentCity})
              //this.props.setCurrentGeo(this.state.location)
          }
        })
      }
    })
  }
  
  componentWillMount() {
    setTitle('东方财富证券财富社区')
    //选择城市页面返回后设置城市
    let nowtime = new Date().getTime()
    let geoInfo = this.state.localStorageGeoInfo
    if (geoInfo && geoInfo.selectCity && nowtime < geoInfo.selectCityExpireTime){
      this.setState({
        location: geoInfo.selectCity
      })
      return
    }
    //定位
    emH5GetLocation((json)=>{
      if(this.state.geoUseInterface){
        return
      }
      this.setState({
        geoUseLocation: true
      })
      if(json.haspermission == true && json.longitude && json.latitude){
        // 有权限并且有经纬度信息
        // 若切换过城市，24小时内不显示切换弹框
        if (geoInfo && geoInfo.geoChangeCityTime && geoInfo.geoChangeCityTime < new Date().getTime()){
          this.setState({
            showChangeAlert: true
          })
        }
        this.getDetailLocation(json.longitude, json.latitude)
      } else if (json.haspermission == false && !json.longitude && !json.latitude){
        //无权限并且无位置信息
        //若第一次 没有拒绝过
        if (!(geoInfo && geoInfo.geoRejectTime)){
          this.setState({
            showLocationAlert: true
          })
        }
        //若点击过不允许 48小时内不显示弹框
        if (geoInfo && geoInfo.geoRejectTime && geoInfo.geoRejectTime < new Date().getTime()) {   
          this.setState({
            showLocationAlert: true
          })
        }
      }
    },{
      "forceGeoPermission": 0       //0：不尝试取权限   1：尝试取权限  2：尝试取权限，遭禁止跳系统设置
    })
    //1秒后若没有返回数据 用接口得到的城市
    setTimeout(() => {
      console.log('timeout')
      if (this.state.geoUseLocation) {
        return
      }
      this.setState({
        geoUseInterface:true,
        location:'请求接口得到的城市'
      }) 
      this.props.setGeoObject({ 'historyCity':'请求接口得到的城市'})
    }, 1000)
  }
  componentDidMount(){
    window.scrollTo(0,0)
  }
  render () {
    let { ImgList } = this.state
    const opt = {
      auto: 2000,
      loop: true,
      callback: function (index) {
        this.setState({ index: index });
      }.bind(this)
    }
    return (
      <div className="index-header">
        <div className="search-input">
          <span className="location-box" onClick={()=>{this.toOtherPage('/selectcity')}}>{this.state.location}</span>
          <div className="searchinput-box">
            <EmInput onClick={()=>{this.toOtherPage('/search')} } />
          </div>
        </div>
        <ul className="header-tab">
          <li onClick={() => { this.toOtherPage('/communityrecommend')}}>
            <span>推荐关注</span>
          </li>
          <li onClick={() => {this.toPersonalPage()}}>
            <span>我的帖子</span>
          </li>
          <li>
            <span className="has-dot" onClick={() => { this.toMyActivityPage()}}>我的活动</span>
          </li>
        </ul>
        <div className="swiper-indexcontainer">
          <ReactSwipe swipeOptions={opt}>
            {
              ImgList.length && ImgList.map((item, index) => {
                return (
                  <img className="img-list" key={index} src={item} alt="" />
                )
              })
            }
          </ReactSwipe>
          <ul className="index-container">
            {
              ImgList.length && ImgList.map((dotitem, dotindex) => {
                return (
                  <li className={this.state.index === dotindex ? "selected" : ''} key={dotindex}></li>
                )
              })
            }
          </ul>
        </div>
        <div className={this.state.showLocationAlert ? 'mask' : ''}></div>
        <div className={this.state.showLocationAlert ? 'show-maploaction' : 'hide'}>
          <img className="maplocation-icon" src={require('../../assets/map-location.png')} alt="" />
          <p className="loaction-tit">开启地理位置权限</p>
          <p className="loaction-content">为你推荐身边的营业部<br />发现更多精彩线下活动</p>
          <ul className="mapbtn-list">
            <li onClick={this.closeMap}>不允许</li>
            <li className="allow-list" onClick={this.getPermission}>允许</li>
          </ul>
          <a onClick={this.closeMap} className="close-location"></a>
        </div>
      </div>
    )
  }
}

//映射store.getState()的数据到props
//将store中的数据作为props绑定到组件中，只要store更新了就会调用这个方法，mapStateToProps返回的结果必须是object对象，该对象的值会被更新到组件中
const mapStateToProps = state=>({
  geo:state.geo,
  fortune: state.fortune
})
//映射store.dispatch的函数到props
const mapDispatchToProps = dispatch => ({
  getGeoInfoAll:()=>dispatch(geo.getGeoInfoAll()),
  //getGeoInfo: (key) => dispatch(geo.getGeoInfo(key)),
  //setGeoKeyValue: (key,value) => dispatch(geo.setGeoKeyValue(key,value)),
  setGeoObject: (obj) => dispatch(geo.setGeoObject(obj)),
  // getCurrentGeo: () => dispatch(geo.getCurrent()),
  //setCurrentGeo: cityname => dispatch(geo.setCurrentCity(cityname))
  //getCFHUserInfoByUid:(obj)=>dispatch(fortune.getCFHUserInfoByUid(obj))
})

//connect方法：连接React组件与Redux store
//withRouter:被修饰的组件可以从路由中获取history,location,match等属性；路由组件可以直接获取这些属性，但非路由组件就必须通过withRouter修饰
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(IndexHeader))
