let config = {};
const version = '1.0.2';

if (location.protocol.indexOf('http') < 0) { //落地  
    config = {
        api: '',
        api_person: 'https://emcreative.eastmoney.com/',     //个人页API   
    }
}
else if (location.host.indexOf('172.30') > -1 || location.host.indexOf('127.0.0.1') > -1 || location.host.indexOf('localhost') > -1) { //本地
    config = {
        api: 'http://172.30.66.37:3000/',
        api_person: 'http://172.30.66.38:8013/',
    }
}
else if (location.host.indexOf('emtest2.eastmoney.com') > -1) { //测试线
    config = {
        api: 'http://emtest2.eastmoney.com/',
        api_person: 'http://emtest2.eastmoney.com/',
    }
}
else if (location.host.indexOf('emcreative.eastmoney.com:10443') > -1) { //灰度线
    config = {
        api: 'https://emcreative.eastmoney.com:10443/',
        api_person: 'https://emcreative.eastmoney.com:10443/',
    }
}
else if (location.host.indexOf('emcreative.eastmoney.com') > -1) { //正式线
    config = {
        api: 'https://emcreative.eastmoney.com/',
        api_person: 'https://emcreative.eastmoney.com/',   //个人页API
    }
}
config.origin = config.api;
config.css = `${config.css_orgin}/fortune/resource/css/style.css?v=${version}`
config.api_pe=config.url_pe;

config.getWechatlogo = (type) => { //0 主包中分享 1 uufund中分享
    if (type == 1) {
        return 'https://resource.uufund.com/APP/resource/img/logo.png'
    }
    return 'https://emfed.eastmoney.com/public/resource/img/wechatlogo.png';
}



// config.langke_css = config.api + '/Fortune/Html/elplayer/css/elplayer.min.css';
// config.langke_swf = config.api + '/Fortune/Html/elplayer/emlive.swf';

config.gubaApiUrl = 'http://gubawebapi.eastmoney.com/';

export default config